<template>
  <div class="container">
    <div class="card card-custom mb-3">
      <div class="card-body">
        <h4 class="card-title mb-0 font-weight-bolder mb-3">{{ config.title }}</h4>
        <!-- <hr class="my-3"> -->
        <global-filter query inline @change="globalFilterChange($event)" />
        <hr style="border-style: dashed">
        <div class="d-flex align-items-center flex-fill">
          <span class="font-weight-bold font-size-lg pr-3" style="min-width:110px" for="">Pilih Agenda :</span>
          <v-select :clearable="false" v-model="selectedAgenda" :appendToBody="true" class="vs-style custom flex-fill" :options="agendaList" :placeholder="'Pilih salah satu Agenda'">
            <slot name="no-options">{{ $t('error.no_data') }}</slot>
          </v-select>
        </div>
      </div>
    </div>
    <div v-if="isValidFilter" class="card card-custom mb-3">
      <div class="card-body">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'crud-auction-plans',
  data () {
    return {
      config: {
        title: 'Rencana Lelang Diikuti',
        permission: {
          create: 'create-auction-plans',
          read: 'view-auction-plans',
          update: 'do-verification-request-auction-plans',
          delete: 'delete-auction-plans'
        }
      },
      fullFilter: {},
      searchFilter: '',
      usedFilter: {
        search: '',
        page: 1,
        limit: 10
      },
      selectedAgenda: { label: 'RKAP Kuantitatif', code: null },
      agendaList: [],
      isLoading: false,
      dataList: {
        total: 0,
        totalPage: 1,
        data: []
      }
    }
  },
  computed: {
    isValidFilter () {
      return !!(this.usedFilter.periode_year && this.usedFilter.division_id)
    }
  },
  watch: {
    searchFilter: {
      deep: false,
      immediate: false,
      handler (e) {
        this.usedFilter = Object.assign({}, this.usedFilter, {
          search: e,
          page: 1,
          limit: 10
        })
      }
    },
    selectedAgenda: {
      deep: true,
      immediate: false,
      handler (e) {
        const res = (typeof e === 'object') ? e.code : e
        this.usedFilter = Object.assign({}, this.usedFilter, {
          agenda_id: res,
          page: 1,
          limit: 10
        })
      }
    },
    usedFilter: {
      deep: true,
      immediate: false,
      handler (e) {
        this.getListData(e)
      }
    }
  },
  methods: {
    globalFilterChange (e) {
      this.fullFilter = Object.assign({}, e)
      this.getAgenda(e, null, true)
    },
    getAgenda (e, selected = null, fromGlobal = false) {
      const tempFilter = Object.assign({}, {
        // division_id: fromGlobal ? e.division_id && e.division_id.code ? e.division_id.code : null : e.division_id,
        periode_year: fromGlobal ? e.year : e.periode_year,
        agenda_id: selected || null
      })
      this.$_api.dataset('m-quantitatif-agendas', tempFilter).then(ag => {
        let agenda = ag.data.map(x => { return { label: x.name, code: x.id } })
        this.agendaList = [...[{ label: 'RKAP Kualitatif', code: null }], ...agenda]
        if (!selected) this.selectedAgenda = this.agendaList[0]
        else this.selectedAgenda = this.agendaList.filter(x => x.code === selected)[0]
        // ======
        this.usedFilter = Object.assign({}, tempFilter, {
          search: '',
          page: 1,
          limit: 10,
          id: this.$route.query.id || null
        })
        // lalala yeyeye
      })
    },
    getListData (e) {
      this.isLoading = true
      this.$_api.get('custom/auction-plans/list', e).then(res => {
        this.isLoading = false
        this.dataList = res
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    }
  }
}
</script>

<style>
</style>



